@custom-media --xs (min-width: 480px);
@custom-media --xs-down (max-width: 479px);
@custom-media --sm (min-width: 640px);
@custom-media --sm-down (max-width: 639px);
@custom-media --md (min-width: 768px);
@custom-media --md-down (max-width: 767px);
@custom-media --lg (min-width: 1024px);
@custom-media --lg-down (max-width: 1023px);
@custom-media --xl (min-width: 1280px);
@custom-media --xl-down (max-width: 1279px);
@custom-media --2-xl (min-width: 1536px);
@custom-media --2-xl-down (max-width: 1535px);
.wp-block-list {
  color: var(--text-color, currentcolor);
  padding-left: var(--wp--preset--spacing--xs);
}
.wp-block-list li {
  margin-top: var(--wp--preset--spacing--md);
}
.wp-block-list:where(ol) {
  padding-left: var(--wp--preset--spacing--sm);
}
.wp-block-list:where(ol) li {
  margin-top: var(--wp--preset--spacing--md);
}
.wp-block-list:where(ul) {
  --icon-size: 12px;
  list-style: none;
}
.wp-block-list:where(ul) li {
  break-inside: avoid;
  color: inherit;
  isolation: isolate;
  padding-left: calc(var(--icon-size) + var(--wp--preset--spacing--lg));
  position: relative;
}
.wp-block-list:where(ul) li::before {
  background-color: var(--wp--custom--color--surface--primary);
  content: "";
  display: block;
  height: var(--icon-size);
  left: 0;
  mask-image: url("../../../assets/svg/radial.svg");
  mask-repeat: no-repeat;
  mask-size: contain;
  position: absolute;
  top: 11px;
  width: var(--icon-size);
}

.panel-header .wp-block-list li {
  font-size: var(--wp--preset--font-size--xl);
  color: var(--wp--custom--color--text--primary);
  font-weight: 500;
  padding-left: calc(var(--icon-size) + var(--wp--preset--spacing--lg));
  margin-block-start: var(--wp--preset--spacing--sm);
  white-space: nowrap;
  top: 20%;
}
.panel-header .wp-block-list li::before {
  background-color: var(--wp--custom--color--surface--primary);
  --icon-size: 20px;
  top: 20%;
}

.wp-block-list {
  margin-block-start: var(--wp--preset--spacing--sm);
}

::marker {
  font-weight: 600; /* Change the weight of the numbers */
  color: var(--wp--custom--color--text--primary); /* Optional: Change the color of the numbers */
}